@import '../../styles/variables.scss';

.app {
  &__header {
    display: flex;
    justify-content: space-between;
  }
  &__title {
    font-weight: bold;
    font-size: 28px;
    line-height: 37px;
    span {
      color: $main-color;
    }
  }
  &__menu {

      li {
      }
  }
}

.navigation {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  &__item {
    margin: 0 8px;
    &_active {
      color: $main-color;
    }
    a:hover {
      filter: brightness(85%);
    }
  }
}
