@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
.app__header{display:flex;justify-content:space-between}.app__title{font-weight:bold;font-size:28px;line-height:37px}.app__title span{color:#9F0013}.navigation{display:flex;justify-content:space-between;font-weight:bold;font-size:24px;line-height:32px}.navigation__item{margin:0 8px}.navigation__item_active{color:#9F0013}.navigation__item a:hover{-webkit-filter:brightness(85%);filter:brightness(85%)}

.random-char{margin-top:50px;box-shadow:5px 5px 20px rgba(0,0,0,0.25);display:grid;grid-template-columns:50% 50%}.random-char__left{padding:35px 40px;display:flex;grid-gap:30px;gap:30px}.random-char__img{max-width:180px;width:100%;max-height:180px}.random-char__descr{height:80px;margin-top:10px;display:-webkit-box;-webkit-line-clamp:5;-webkit-box-orient:vertical;overflow:hidden;text-overflow:ellipsis}.random-char__buttons{margin-top:30px;display:flex;justify-content:space-around;grid-gap:20px;gap:20px}.random-char__right{padding:35px 40px;background-color:#232222;position:relative}.random-char__decoration{position:absolute;right:-45px;bottom:0}.random-char__title:nth-child(2){margin-top:33px;margin-bottom:28px}

.app__error-message{display:flex;justify-content:center;align-items:center;flex-direction:column}.app__error-message span{font-size:40px;letter-spacing:2px;font-weight:700;text-transform:uppercase}.app__error-message p{color:#5C5C5C;font-weight:500;font-size:24px;margin-top:15px;text-transform:uppercase}

.char__content{margin-top:50px;display:grid;grid-template-columns:650px 425px;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px;align-items:start}.char__grid{display:grid;grid-gap:25px 31px;gap:25px 31px;grid-template-columns:repeat(3, 200px);min-height:1000px}.char__item{width:200px;height:320px;background-color:#232222;box-shadow:5px 5px 10px rgba(0,0,0,0.25);cursor:pointer;transition:0.3s -webkit-transform;transition:0.3s transform;transition:0.3s transform, 0.3s -webkit-transform}.char__item img{width:200px;height:200px;object-fit:contain}.char__item span{display:block;margin:15px}.char__item_selected{box-shadow:0 5px 20px #9F0013;-webkit-transform:translateY(-8px);transform:translateY(-8px)}

.skeleton__card-img{width:100%;height:200px;background-color:#C4C4C4}.skeleton__card-title{margin-top:20px;height:20px;width:100px;background-color:#C4C4C4}

.char__info{position:sticky;top:10px;padding:25px;box-shadow:0 0 20px rgba(0,0,0,0.25);z-index:5;background-color:#fff}.char__info .skeleton{margin-top:30px}.char__basics{display:grid;grid-template-columns:150px auto;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px}.char__basics img{width:150px;height:150px;object-fit:cover}.char__btns{margin-top:35px}.char__btns a:nth-child(2){margin-top:10px}.char__descr{margin-top:15px;font-size:14px;line-height:18px}.char__comics{font-weight:bold;font-size:18px;line-height:24px;margin-top:10px}.char__comics-list{position:relative;margin-top:10px}.char__comics-item{width:100%;padding:0 10px;line-height:25px;box-shadow:0 4px 4px rgba(0,0,0,0.25);margin-top:10px;transition:.3s ease;cursor:pointer}.char__comics-item:hover{box-shadow:2px 6px 6px rgba(0,0,0,0.25)}.char__select{font-weight:bold;font-size:18px;line-height:24px;text-align:center}

.skeleton__header{display:grid;grid-template-columns:40px auto;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;align-items:center;margin-bottom:30px}.skeleton__circle{width:40px;height:40px;background-color:#C4C4C4;border-radius:100%}.skeleton__mini{width:100%;height:16px;background-color:#C4C4C4;margin-top:15px}.skeleton__block{height:35px;width:100%;background-color:#C4C4C4}

.app__banner{width:100%;background-color:#232222;height:100px;padding:0 25px 0 45px;display:grid;grid-template-columns:152px auto 133px}.app__banner-text{font-weight:bold;font-size:24px;line-height:32px;color:#FFFFFF;padding-left:83px;padding-top:18px}

.comics__list{margin-top:45px}.comics__grid{display:grid;grid-template-columns:repeat(auto-fill, 225px);justify-content:space-between;grid-row-gap:55px;row-gap:55px}.comics__item{transition:0.3s -webkit-transform;transition:0.3s transform;transition:0.3s transform, 0.3s -webkit-transform}.comics__item-img{box-shadow:5px 5px 10px rgba(0,0,0,0.25);width:225px;height:345px}.comics__item-name{margin-top:10px;font-weight:bold;font-size:14px;line-height:18px}.comics__item-price{margin-top:5px;font-weight:bold;font-size:14px;line-height:18px;color:rgba(0,0,0,0.6);text-transform:uppercase}.comics__item:hover{-webkit-transform:translateY(-5px);transform:translateY(-5px)}

.single-comic{margin-top:50px;display:grid;grid-template-columns:293px 550px auto;grid-column-gap:50px;-webkit-column-gap:50px;column-gap:50px;align-items:start}.single-comic__img{width:293px;height:450px}.single-comic__name{font-weight:bold;font-size:22px;line-height:29px}.single-comic__descr{font-size:18px;line-height:24px;margin-top:25px}.single-comic__price{font-weight:bold;font-size:24px;line-height:32px;color:#9F0013;margin-top:25px}.single-comic__back{justify-self:end;font-weight:bold;font-size:18px;line-height:24px}.single-comic__back:hover{color:#9F0013}

.button{min-width:101px;color:#fff;text-align:center;text-transform:uppercase;font-size:14px;transition:0.3s;border:none;background-color:transparent;cursor:pointer}.button__long{display:block;width:170px;margin:45px auto 0 auto}.button .inner{position:relative;display:block;background-color:#9F0013;line-height:18px;padding:0 18px;transition:none}.button .inner::before{border-color:#9F0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;left:0;top:-10px;transition:none}.button .inner::after{border-color:#9F0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;right:0;bottom:-10px;-webkit-transform:rotate(180deg);transform:rotate(180deg);transition:none}.button__secondary .inner{background-color:#5C5C5C}.button__secondary .inner::before{border-color:#5C5C5C transparent;transition:none}.button__secondary .inner::after{border-color:#5C5C5C transparent;transition:none}.button::before{content:'';display:block;height:10px;margin-left:10px;transition:none}.button::after{content:'';display:block;height:10px;margin-right:10px;transition:none}.button.button__main::before,.button.button__main::after{background-color:#9F0013}.button.button__secondary::before,.button.button__secondary::after{background-color:#5C5C5C}.button:hover{-webkit-filter:brightness(85%);filter:brightness(85%)}.text{font-size:14px;font-weight:400;line-height:16px;letter-spacing:0}.text_white{color:#fff}.title{font-weight:700;letter-spacing:0}.title_white{color:#fff}.title_fs-22{font-size:22px;line-height:26px}.title_fs-24{font-size:24px;line-height:28px}.title_upper{text-transform:uppercase}*{font-family:"Roboto Condensed", sans-serif;margin:0;padding:0;box-sizing:border-box}ul,li,dl{list-style-type:none;margin:0;padding:0}h1,h2,h3,h4,h5,h6{margin:0}a{text-decoration:none;color:inherit;display:inline-block}a:hover{text-decoration:none}p{margin:0;padding:0}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.app{width:1100px;margin:0 auto;padding:50px 0;position:relative}.app .bg-decoration{position:absolute;right:-70px;bottom:-100px;z-index:-1}main{margin-top:50px;position:relative}.pulse{-webkit-animation:pulse 1.5s ease-in-out .5s infinite;animation:pulse 1.5s ease-in-out .5s infinite}@-webkit-keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}@keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}

