@import '../../styles/variables.scss';

.char {
  &__info {
    position: sticky;
    top: 10px;
    padding: 25px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
    z-index: 5;
    background-color: #fff;
    .skeleton {
      margin-top: 30px;
    }
  }
  &__basics {
    display: grid;
    grid-template-columns: 150px auto;
    column-gap: 25px;
    img {
      width: 150px;
      height: 150px;
      object-fit: cover;
    }
  }
  &__btns {
    margin-top: 35px;
    a:nth-child(2) {
      margin-top: 10px;
    }
  }
  &__descr {
    margin-top: 15px;
    font-size: 14px;
    line-height: 18px;
  }
  &__comics {
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    margin-top: 10px;
    &-list {
      position: relative;
      margin-top: 10px;
    }
    &-item {
      width: 100%;
      padding: 0 10px;
      line-height: 25px;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      margin-top: 10px;
      transition: .3s ease;
      cursor: pointer;
      &:hover {
        box-shadow: 2px 6px 6px rgba(0, 0, 0, 0.25);
      }
    }
  }
  &__select {
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
  }
}
